<template>
  <div class="shared-document-container">
    <div v-if="loading" class="loading-state">
      <i class="pi pi-spin pi-spinner" style="font-size: 3rem; color: #4285F4;"></i>
      <p>Chargement du document partag√©...</p>
    </div>

    <div v-else-if="error" class="error-state">
      <i class="pi pi-exclamation-circle" style="font-size: 3rem; color: #f44336;"></i>
      <h2>{{ error }}</h2>
      <p>Le lien de partage est peut-√™tre expir√© ou invalide.</p>
      <router-link to="/connexion" class="btn btn-primary">
        <i class="pi pi-sign-in"></i> Se connecter
      </router-link>
    </div>

    <!-- Modal pour saisir le code d'acc√®s -->
    <div v-if="showCodeModal" class="code-modal-overlay" @click.self="showCodeModal = false">
      <div class="code-modal">
        <h3>Code d'acc√®s requis</h3>
        <p>Ce document est prot√©g√© par un code √† 6 chiffres.</p>
        <input 
          v-model="accessCode" 
          type="text" 
          maxlength="6" 
          placeholder="Entrez le code √† 6 chiffres"
          class="code-input"
          @keyup.enter="submitAccessCode"
        />
        <p v-if="codeError" class="error-message">{{ codeError }}</p>
        <div class="modal-actions">
          <button @click="submitAccessCode" class="btn btn-primary" :disabled="accessCode.length !== 6">
            <i class="pi pi-check"></i> Valider
          </button>
          <button @click="showCodeModal = false" class="btn btn-secondary">
            <i class="pi pi-times"></i> Annuler
          </button>
        </div>
      </div>
    </div>

    <div v-else-if="document" class="document-view">
      <div class="document-header">
        <div class="document-info">
          <i :class="getFileIcon(document.nom)" class="file-icon"></i>
          <div>
            <h1>{{ document.nom }}</h1>
            <p class="document-meta">
              Partag√© par <strong>{{ document.owner?.name || 'Utilisateur' }}</strong>
              <span v-if="document.created_at"> ‚Ä¢ {{ formatDate(document.created_at) }}</span>
            </p>
          </div>
        </div>
        <div class="document-actions">
          <button @click="downloadDocument" class="btn btn-primary">
            <i class="pi pi-download"></i> T√©l√©charger
          </button>
        </div>
      </div>

      <div class="document-content">
        <!-- Pr√©visualisation pour les images -->
        <div v-if="isImage(document.nom)" class="image-preview">
          <img :src="documentUrl" :alt="document.nom" />
        </div>

        <!-- Pr√©visualisation pour les PDF -->
        <div v-else-if="isPDF(document.nom)" class="pdf-preview">
          <iframe :src="documentUrl" frameborder="0"></iframe>
        </div>

        <!-- Pr√©visualisation pour les vid√©os -->
        <div v-else-if="isVideo(document.nom)" class="video-preview">
          <video controls :src="documentUrl"></video>
        </div>

        <!-- Pour les autres types de fichiers -->
        <div v-else class="file-info">
          <i :class="getFileIcon(document.nom)" style="font-size: 5rem; color: #4285F4;"></i>
          <h3>{{ document.nom }}</h3>
          <p>Type: {{ getFileType(document.nom) }}</p>
          <p v-if="document.taille">Taille: {{ formatFileSize(document.taille) }}</p>
          <button @click="downloadDocument" class="btn btn-primary btn-lg mt-3">
            <i class="pi pi-download"></i> T√©l√©charger le fichier
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'SharedDocumentView',
  data() {
    return {
      document: null,
      loading: true,
      error: null,
      token: null,
      showCodeModal: false,
      accessCode: '',
      codeError: '',
      previewBlobUrl: null
    };
  },
  computed: {
    documentUrl() {
      if (!this.document) return '';
      // Utiliser l'URL blob pour la pr√©visualisation au lieu de l'URL directe
      return this.previewBlobUrl || '';
    }
  },
  mounted() {
    this.token = this.$route.params.token;
    this.fetchSharedDocument();
  },
  methods: {
    async fetchSharedDocument() {
      this.loading = true;
      this.error = null;

      try {
        console.log('R√©cup√©ration du document avec token:', this.token);
        // Utiliser /info pour r√©cup√©rer les m√©tadonn√©es
        const response = await axios.get(`http://localhost:8000/api/shared-documents/${this.token}/info`);
        
        console.log('üì• R√©ponse API compl√®te:', response.data);
        console.log('üìã Structure de response.data.data:', response.data.data);
        
        // V√©rifier si les donn√©es du document sont pr√©sentes
        if (response.data.data && response.data.data.document) {
          this.document = response.data.data.document;
          this.document.owner = { name: response.data.data.shared_by };
          console.log('üìÑ Document charg√©:', this.document);
          
          // V√©rifier les restrictions de s√©curit√© dans diff√©rents endroits possibles
          const requireCode = response.data.data.require_code || response.data.require_code || false;
          const requireLogin = response.data.data.require_login || response.data.require_login || false;
          
          console.log('üîí Restrictions de s√©curit√© d√©tect√©es:', { 
            requireCode, 
            requireLogin,
            'depuis data.data': {
              require_code: response.data.data.require_code,
              require_login: response.data.data.require_login
            },
            'depuis data': {
              require_code: response.data.require_code,
              require_login: response.data.require_login
            }
          });
          
          // Si un code est requis, afficher la modal
          if (requireCode) {
            this.showCodeModal = true;
            this.loading = false;
            return;
          }
          
          // Si la connexion est requise et l'utilisateur n'est pas connect√©
          // Le backend devrait retourner 401, mais on v√©rifie aussi ici
          if (requireLogin) {
            // V√©rifier si l'utilisateur est connect√©
            const sessionStr = localStorage.getItem('yamsdigital_session');
            if (!sessionStr) {
              this.$router.push({
                path: '/connexion',
                query: { redirect: this.$route.fullPath }
              });
              return;
            }
          }
          
          // Charger le fichier pour pr√©visualisation uniquement si aucune restriction
          await this.loadDocumentPreview();
        } else {
          this.error = response.data.message || 'Document non trouv√©';
        }
      } catch (error) {
        console.error('Erreur compl√®te:', error);
        console.error('R√©ponse erreur:', error.response);
        
        // Gestion des cas sp√©ciaux de protection
        if (error.response?.status === 401) {
          // Connexion requise - rediriger vers login
          this.$router.push({
            path: '/connexion',
            query: { redirect: this.$route.fullPath }
          });
          return;
        } else if (error.response?.status === 404) {
          this.error = 'Document non trouv√©';
        } else if (error.response?.status === 403) {
          // Si le backend indique require_code, afficher la modal code
          const data = error.response?.data;
          if (data && data.require_code) {
            this.showCodeModal = true;
            this.loading = false;
            return;
          } else {
            this.error = 'Acc√®s refus√©';
          }
        } else if (error.response?.status === 410) {
          this.error = 'Le lien de partage a expir√©';
        } else if (!error.response) {
          this.error = 'Impossible de contacter le serveur. V√©rifiez que le backend est d√©marr√©.';
        } else {
          this.error = error.response?.data?.message || 'Erreur lors du chargement du document';
        }
      } finally {
        this.loading = false;
      }
    },

    async loadDocumentPreview() {
      try {
        console.log('Chargement de la pr√©visualisation du document...');
        
        // Pr√©parer les headers et l'URL
        let url = `http://localhost:8000/api/shared-documents/${this.token}`;
        const headers = {};
        
        // Ajouter le code d'acc√®s si disponible
        if (this.accessCode) {
          const codeAsNumber = parseInt(this.accessCode, 10);
          url += `?access_code=${codeAsNumber}`;
          headers['X-Access-Code'] = this.accessCode;
        }
        
        const response = await axios.get(url, {
          responseType: 'blob',
          headers
        });
        
        // Cr√©er une URL blob pour la pr√©visualisation
        this.previewBlobUrl = window.URL.createObjectURL(response.data);
        console.log('Pr√©visualisation charg√©e avec succ√®s');
      } catch (error) {
        console.error('Erreur lors du chargement de la pr√©visualisation:', error);
        
        // Si erreur 403 et code requis, afficher la modal
        if (error.response?.status === 403 && error.response?.data?.require_code) {
          this.showCodeModal = true;
        }
        // Ne pas afficher d'erreur, juste ne pas montrer la pr√©visualisation
      }
    },

    async submitAccessCode() {
      if (this.accessCode.length !== 6) {
        this.codeError = 'Le code doit contenir exactement 6 chiffres';
        return;
      }

      this.codeError = '';
      this.loading = true;
      this.showCodeModal = false;

      console.log('üîë Envoi du code d\'acc√®s:', this.accessCode);

      try {
        const codeAsNumber = parseInt(this.accessCode, 10);
        
        // √âtape 1: R√©cup√©rer les informations du document avec le code
        console.log('üì• R√©cup√©ration des informations du document avec le code...');
        console.log('üîë Code envoy√©:', this.accessCode, '(nombre:', codeAsNumber, ')');
        console.log('üìç URL appel√©e:', `http://localhost:8000/api/shared-documents/${this.token}/info?access_code=${codeAsNumber}`);
        
        const infoResponse = await axios.get(`http://localhost:8000/api/shared-documents/${this.token}/info?access_code=${codeAsNumber}`, {
          headers: {
            'X-Access-Code': this.accessCode
          }
        });
        
        console.log('üì• R√©ponse /info avec code:', infoResponse.data);
        
        // Mettre √† jour les informations du document
        if (infoResponse.data.data && infoResponse.data.data.document) {
          this.document = infoResponse.data.data.document;
          this.document.owner = { name: infoResponse.data.data.shared_by };
          console.log('‚úÖ Informations du document r√©cup√©r√©es');
        }
        
        // √âtape 2: Charger le fichier pour la pr√©visualisation
        console.log('üì• Chargement du fichier pour pr√©visualisation...');
        const url = `http://localhost:8000/api/shared-documents/${this.token}?access_code=${codeAsNumber}`;
        const response = await axios.get(url, {
          responseType: 'blob',
          headers: {
            'X-Access-Code': this.accessCode
          }
        });
        
        console.log('‚úÖ Fichier re√ßu, status:', response.status);
        
        // Si succ√®s, cr√©er l'URL blob pour la pr√©visualisation
        if (response.status === 200) {
          this.previewBlobUrl = window.URL.createObjectURL(response.data);
          console.log('‚úÖ Document d√©verrouill√© avec le code, pr√©visualisation charg√©e');
        } else {
          this.error = 'Document non trouv√© ou code incorrect';
        }
      } catch (error) {
        console.error('‚ùå Erreur avec code d\'acc√®s:', error);
        console.error('üìã D√©tails de l\'erreur:', error.response?.data);
        console.error('üî¥ Status HTTP:', error.response?.status);
        console.error('üî¥ Message:', error.response?.data?.message);
        
        if (error.response?.status === 403) {
          const errorMsg = error.response?.data?.message || 'Code incorrect';
          console.error('üö´ Acc√®s refus√©:', errorMsg);
          
          // Si le backend dit que le code est requis, c'est qu'il ne l'a pas re√ßu ou valid√©
          if (error.response?.data?.require_code) {
            this.codeError = 'Le code n\'a pas √©t√© valid√© par le serveur. V√©rifiez que le code est correct.';
          } else {
            this.codeError = errorMsg;
          }
          this.showCodeModal = true;
        } else {
          this.error = error.response?.data?.message || 'Erreur lors du chargement du document';
        }
      } finally {
        this.loading = false;
      }
    },

    async downloadDocument() {
      try {
        let url = `http://localhost:8000/api/shared-documents/${this.token}`;
        const headers = {};
        
        // Ajouter le code d'acc√®s si pr√©sent
        if (this.accessCode) {
          const codeAsNumber = parseInt(this.accessCode, 10);
          url += `?access_code=${codeAsNumber}`;
          headers['X-Access-Code'] = this.accessCode;
        }
        
        // Utiliser l'URL sans /download pour t√©l√©charger directement
        const response = await axios.get(url, {
          responseType: 'blob',
          headers
        });

        const blobUrl = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement('a');
        link.href = blobUrl;
        link.setAttribute('download', this.document.nom);
        document.body.appendChild(link);
        link.click();
        link.remove();
        window.URL.revokeObjectURL(blobUrl);
      } catch (error) {
        console.error('Erreur lors du t√©l√©chargement:', error);
        alert('Erreur lors du t√©l√©chargement du fichier');
      }
    },

    isImage(filename) {
      const ext = filename.split('.').pop().toLowerCase();
      return ['jpg', 'jpeg', 'png', 'gif', 'bmp', 'webp', 'svg'].includes(ext);
    },

    isPDF(filename) {
      return filename.toLowerCase().endsWith('.pdf');
    },

    isVideo(filename) {
      const ext = filename.split('.').pop().toLowerCase();
      return ['mp4', 'webm', 'ogg', 'mov'].includes(ext);
    },

    getFileIcon(filename) {
      const ext = filename.split('.').pop().toLowerCase();
      const iconMap = {
        pdf: 'pi pi-file-pdf',
        doc: 'pi pi-file-word',
        docx: 'pi pi-file-word',
        xls: 'pi pi-file-excel',
        xlsx: 'pi pi-file-excel',
        ppt: 'pi pi-file',
        pptx: 'pi pi-file',
        jpg: 'pi pi-image',
        jpeg: 'pi pi-image',
        png: 'pi pi-image',
        gif: 'pi pi-image',
        mp4: 'pi pi-video',
        mp3: 'pi pi-volume-up',
        zip: 'pi pi-file',
        rar: 'pi pi-file'
      };
      return iconMap[ext] || 'pi pi-file';
    },

    getFileType(filename) {
      const ext = filename.split('.').pop().toUpperCase();
      return ext;
    },

    formatFileSize(bytes) {
      if (!bytes) return 'N/A';
      const sizes = ['Bytes', 'KB', 'MB', 'GB'];
      const i = Math.floor(Math.log(bytes) / Math.log(1024));
      return Math.round(bytes / Math.pow(1024, i) * 100) / 100 + ' ' + sizes[i];
    },

    formatDate(dateString) {
      if (!dateString) return '';
      const date = new Date(dateString);
      return new Intl.DateTimeFormat('fr-FR', {
        day: '2-digit',
        month: 'long',
        year: 'numeric'
      }).format(date);
    }
  },
  beforeUnmount() {
    // Nettoyer l'URL blob pour √©viter les fuites m√©moire
    if (this.previewBlobUrl) {
      window.URL.revokeObjectURL(this.previewBlobUrl);
    }
  }
};
</script>

